import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import {Link } from "react-router-dom"; 
import Loader from 'react-loader-spinner';
import { FiChevronsRight } from "react-icons/fi";
import Moment from 'react-moment';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import Propertyviewimage from "./Propertyviewimage";
import '../../styles/_property.scss';

import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
let BaseURLuploads = WEB_URL_UPLOAD;
let breadcroumbimg = BaseURLuploads + 'bread2.jpg';
const Rent = () => {
  const {Rentsall} = useContext(MyContext);
  const [page, setPage] = useState(1);
      useEffect(() => {
        Rents(page);
        window.scrollTo(0, 0);
      }, []);

      const [isRents, setRents] = useState([]);
      const [loademoreloding, setloademoreloding] = useState(false);
      const [ispostloademore, setpostloademore] = useState(false);
      const Rents = async(page) =>{
        try {
          setloademoreloding(true);
          const Rentssss = await Rentsall(page);
          if(Rentssss.success === 1){
            console.log('Rentssss',Rentssss.Rentsviews);
            if(Rentssss.Rentsviews.length > 0){
              setRents(prevBooks => {
                return [...prevBooks, ...Rentssss.Rentsviews]
              })
                   setpostloademore(false);
                   setloademoreloding(false);
              }else{
                    setpostloademore(true);
                    setloademoreloding(false);
              }
          }
        } catch (error) { throw error;}
      }
 const [count, setCount] = useState(1);
 const handleIncrement = (count) => {
        setCount(prevCount => prevCount + 1);
        console.log('count',count+1);
        Rents(count+1);
 };

  return (
    <>
    <Navbarmenu />
    <div className="clearfix"></div>
<div className="breadcroumb-area" style={{backgroundImage:"url(" + breadcroumbimg + ")"}}> 
  <div className="container"> 
    <div className="rows"> 
      <div className="breadcroumb-title text-center">
        <h1>For Rent </h1>
      </div>
    </div>
  </div>
</div>
<div className="Rentsss"> 
  <div className="container"> 
    <div className="rows"> 

{isRents.map((item, index) => (  
  <div className="col-md-12" key={item.property_id} >
    <div  className="col-md-4"  >
       <Propertyviewimage ids={item.property_id} />
    </div>
      {/* <h2> <b> {item.Bedrooms !== '' ? <> {item.Bedrooms} BHK </> : null } {item.propertytype} </b> for rent {item.City} {item.Locality} </h2> */}

{/* ----------- ALL COMMERCIAL -------------------------------------------------------------------------------------------------------------------------------------------- */}
    {item.propertytype === 'Commercial Office Space' || item.propertytype === 'Office in IT Park/ SEZ' ||item.propertytype === 'Commercial Shop' ||item.propertytype === 'Commercial Showroom' ||item.propertytype === 'Commercial Land' ||item.propertytype === 'Warehouse/Godown' ||item.propertytype === 'Industrial Land' ||item.propertytype === 'Industrial Building' ||item.propertytype === 'Industrial Shed' ? <>   
    
    <h2> <b> 
       {item.propertytype === 'Commercial Shop' ? 'Shop' : null} 
       {item.propertytype === 'Commercial Showroom' ? 'Showroom' : null} 
    
    
    </b> for rent {item.City} {item.Locality} </h2>
      <ul>
      {item.Carpet !== '' ? <li>Carpet Area : {item.Carpet} {item.Carpetsq}</li>
      : <li>Super Area : {item.Super} {item.Supersq}</li>
      }
        <li> Floor : {item.FloorNo === 'Lower Basement' || item.FloorNo === 'Upper Basement'  || item.FloorNo === 'Ground' ? <> {item.FloorNo}  </> : <> Ground out of {item.FloorNo}  </> } </li>
        <li>{item.availablefrom === 'Immediately'? <> Immediately Available </> : <> Available From : <Moment format="DD MMM YYYY" withTitle>
        {item.datevalues}
       </Moment>  </> }</li>
       {item.lock !== '' ? 
        <li>Lock-in Period : {item.lock} Years </li>
        : null}
        {item.widthofentrance !== '' ? 
        <li>Entrance : {item.widthofentrance}  </li>
        : null}
        <li>Property Age : {item.Age} </li>
        <li> {item.FurnishedStatus} </li>
        <li></li>
      </ul>
    </> : null }
    
  </div> 
))}
       
       {ispostloademore === false ? 
       <div className="loading" >
              {loademoreloding  ?
              <>
                     <Loader type="ThreeDots" color="#1da1f3" height={67} width={67} style={{float: "left",marginTop:"37px",display:"block",width:"100%"}}/>
              </>: 
                     <button className="pagination__buttonsss"  onClick={()=>handleIncrement(count)}> Next <FiChevronsRight /> </button>
              }
       </div>
: null }

    </div>
  </div>
</div>
      
    <Footer />         
			  </>
  )
}

export default Rent
